import {requestMenuList} from '../../utils/request'
const state={
    list:[]
}
const mutations={
    changeMenuList(state,arr){
        state.list=arr
    }
}
const actions={
    listActions(context){
        requestMenuList({istree:true}).then(res=>{
            if(res.data.code==200){
                context.commit('changeMenuList',res.data.list)
            }
        })
    }
} 
const getters={
    list(state){
        return state.list
    }
}

export default {
    state,
    mutations,
    actions,
    getters,
    namespaced:true
}


